import React, { Fragment, useState } from 'react';
import PropTypes from 'prop-types';
import { Link } from 'react-router-dom';
import { connect } from 'react-redux';

import { addEducation } from '../../actions/profile';

function AddEducation({ addEducation, history }) {

    const [formData, setFormData] = useState({
        school: '',
        degree: '',
        fieldofstudy: '',
        from: '',
        to: '',
        current: false,
        description: ''
    });

    const [toDateDisabled, toggleDisabled] = useState(false);

    const { school, degree, fieldofstudy, from, to, current, description } = formData;

    const onChange = e => setFormData({...formData, [e.target.name]: e.target.value});

    return (
        <Fragment>
            <h1 className="large text-primary">
                Adicione suas Formações
      </h1>
            <p className="lead">
                <i className="fas fa-code-branch"></i> Adicione qualquer Formação/Curso que tenha concluído
      </p>
            <small>* = Campo Obrigatório</small>
            <form onSubmit={e => {
                e.preventDefault();
                addEducation(formData, history);
            }} className="form">
                <div className="form-group">
                    <input type="text" placeholder="* Instituição" name="school" required value={school} onChange={e => onChange(e)} />
                </div>
                <div className="form-group">
                    <input type="text" placeholder="* Grau de Certificação" name="degree" required value={degree} onChange={e => onChange(e)} />
                </div>
                <div className="form-group">
                    <input type="text" placeholder="Área de Estudo" name="fieldofstudy" value={fieldofstudy} onChange={e => onChange(e)} />
                </div>
                <div className="form-group">
                    <h4>Data de início</h4>
                    <input type="date" name="from" value={from} onChange={e => onChange(e)} />
                </div>
                <div className="form-group">
                    <p><input type="checkbox" name="current" checked={current} onChange={e => {
                        setFormData({...formData, current: !current});
                        toggleDisabled(!toDateDisabled);
                    }}/> {' '} Estudando Atualmente</p>
                </div>
                <div className="form-group">
                    <h4>Data de Conclusão</h4>
                    <input type="date" name="to" value={to} onChange={e => onChange(e)} disabled={toDateDisabled ? 'disabled': ''}/>
                </div>
                <div className="form-group">
                    <textarea
                        name="description"
                        cols="30"
                        rows="5"
                        placeholder="Descrição da Formação"
                        value={description} 
                        onChange={e => onChange(e)}
                    ></textarea>
                </div>
                <input type="submit" className="btn btn-primary my-1" />
                <Link className="btn btn-light my-1" to="/dashboard">Voltar</Link>
            </form>

        </Fragment>
    )
};

AddEducation.propTypes = {
    addEducation: PropTypes.func.isRequired,
};

export default connect(null, { addEducation })(AddEducation);

